import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { VehicleFormDialogComponent } from './vehicle-form-dialog.component';
import { VehicleFormModule } from '../vehicle-form/vehicle-form.module';
import { SharedModule } from '../../shared/shared.module';
import { MatDialogModule } from '@angular/material/dialog';

@NgModule({
  imports: [
    CommonModule,
    VehicleFormModule,
    SharedModule,
    MatDialogModule
  ],
  declarations: [ VehicleFormDialogComponent ],
  entryComponents: [ VehicleFormDialogComponent ],
  exports: [ VehicleFormDialogComponent ]
})
export class VehicleFormDialogModule { }



