import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { VehicleService } from '../../services/vehicle.service';
import { MatDialog } from '@angular/material/dialog';
import { VehicleFormDialogComponent } from '../vehicle-form-dialog/vehicle-form-dialog.component';

@Component({
  selector: 'app-vehicle-list',
  templateUrl: './vehicle-list.component.html',
  styleUrls: ['./vehicle-list.component.scss']
})
export class VehicleListComponent implements OnInit {

  protected vehicles: { header: string[]; itemsContent: any[]; };
  protected isLoadingVehicles: boolean;

  constructor(private vehicleService: VehicleService,
              private router: Router,
              public addVehicleDialog: MatDialog) {
                this.isLoadingVehicles = true;
  }

  ngOnInit() {
    this.vehicles = {
      header: ['Categoria', 'Veículo', 'Placa', 'Marca', 'Capacidade', 'Ano'],
      itemsContent: []
    };

    this.showVehicles();
  }

  showVehicles() {
    this.vehicles.itemsContent = [];
    this.vehicleService.vehiclesObs.subscribe((data) => {

      for (const vehicleData of data) {
          this.vehicles.itemsContent.push(vehicleData);
      }
      this.isLoadingVehicles = false;
    });
  }

  addNewVehicle() {
    const loginRef = this.addVehicleDialog.open(VehicleFormDialogComponent);
    loginRef.afterClosed().subscribe(result => {
      this.showVehicles();
    });
  }

  goToFull(vehicle) {

  }

}



